
.CodeMirror {
  height: calc(100% - 40px);
  background-color: transparent;
}
.CodeMirror-gutters {
  background-color: transparent;
  border-right: none;
}
.pan-head {
  height: 40px;
  padding: 0 10px;
  font-size: 14px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-justify-content: space-between;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
}
.pans.resizing {
  cursor: ew-resize;
}
.pans.resizing .pan-resizer {
  cursor: ew-resize;
}



